import React, { useContext } from 'react';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import Box from '@material-ui/core/Box';
import Grid from '@material-ui/core/Grid';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Autocomplete from '@material-ui/lab/Autocomplete';
import Divider from '@material-ui/core/Divider';
import Accordion from '@material-ui/core/Accordion';
import AccordionDetails from '@material-ui/core/AccordionDetails';
import AccordionSummary from '@material-ui/core/AccordionSummary';

import GlobalStyles from './../../GlobalStyles';
import { useTheme } from '@material-ui/core/styles';

import ExpandMoreIcon from '@material-ui/icons/ExpandMore';

import WalletConnectPage from "./../WalletConnectPage/WalletConnectPage.tsx"

import { callTransactionWithValue, withdrawContractBalance, contractBalance, withdrawContractTokenBalance, deployWrapper, contractWrapperAddress} from "./../../utils/web3_transaction_maker.js"
import { Context } from './../../store'
var contractAddresses = require('./../../utils/addresses.json');

export default function Saver() {
  const classes = GlobalStyles();
  const [msgValue, setMsgValue] = React.useState(0);
  const [contractAddress, setContractAddress] = React.useState("");
  const [contractTokenAddress, setContractTokenAddress] = React.useState("");
  const [callData, setCallData] = React.useState("");
  const [selectedToken, setSelectedToken] = React.useState("");
  const [selectedSymbol, setSelectedSymbol] = React.useState("");
  const [wcUri, setWcUri] = React.useState("");
  const [expanded, setExpanded] = React.useState(false);
  const [upgraded, setUpgraded] = React.useState(true);
  const [chainName, setChainName] = React.useState("");
  const [wrapperBalance, setWrapperBalance] = React.useState(-1);

  const [state, dispatch] = useContext(Context);

  const handleChange = (panel) => (event, isExpanded) => {
    setExpanded(isExpanded ? panel : false);
  };
  const handleCallData = (e) => {
    setCallData(e.target.value);
  }

  const handleAddress = (e) => {
    setContractAddress(e.target.value);
  }

  const handleTokenWithdrawAddress = (e) => {
    setContractTokenAddress(e.target.value);
  }

  const handleMsgValue = (e) => {
    setMsgValue(e.target.value);
  }

  const handleSubmit = (e) => {
    e.preventDefault();
    var valueInWei = state.web3.utils.toWei(msgValue.toString())
    var dataToContract = callData //.replace(state.account.toString().replace("0x", ""), state.deployedAddress.toString().replace("0x", ""))
    callTransactionWithValue(state.web3, dataToContract, selectedToken, contractAddress, valueInWei, state.deployedAddress, state.chainId);
  }

  const handleWithdraw = (e) => {
    e.preventDefault();
    withdrawContractBalance(state.web3, state.deployedAddress, state.chainId);
  }

  const handleTokenWithdraw = (e) => {
    e.preventDefault();
    withdrawContractTokenBalance(state.web3, state.deployedAddress, contractTokenAddress, state.chainId);
  }

  const handleDeployWrapper = (e) => {
    e.preventDefault();
    deployWrapper(state.web3, state.chainId);
  }

  const handleTokenChange = (e) => {
    try {
      setSelectedToken(e.target.innerText.split("-")[1])
      setSelectedSymbol(e.target.innerText.split("-")[0])
    }
    catch {
      setSelectedToken("")
      setSelectedSymbol("")
    }
  };
  
  var showUi = false

  if (state.connected && state.account !== "0x00") {
    showUi = true
    if (state.deployedAddress === "" && state.hasDeployedContract === "") {
      contractWrapperAddress(state.web3, state.chainId).then((value) => {  dispatch({ type: 'deployed_address', payload: value }) });
    }

    if (state.deployedAddress !== "" && state.hasDeployedContract === "") {
      if (state.deployedAddress === "0x0000000000000000000000000000000000000000") {
        dispatch({ type: 'has_deployed_address', payload: false }) 
      }
      else if (state.deployedAddress.includes("0x")) {
        dispatch({ type: 'has_deployed_address', payload: true }) 
      }
    }
  } else{
    showUi = false
  }

  if (wrapperBalance === -1 && state.hasDeployedContract === true && state.deployedAddress !== "0x0000000000000000000000000000000000000000") {
    if (state.deployedAddress !== "") {
      contractBalance(state.web3, state.deployedAddress, state.chainId).then((value) => { setWrapperBalance(value) });
    }
  }

  var contractDiaglog
  var proxyTitle

  var contractLink = ""
  var linkText = ""

  if(state.chainId === 1 || state.chainId === 4 ){
    linkText = "View on EtherScan"
    contractLink = "https://etherscan.io/address/"+ contractAddresses[state.chainId]['factory'] +"#code"
  }
  else{
    linkText = "View on BscScan"
    contractLink = "https://bscscan.com/address/#code"
  }
  
  if (state.hasDeployedContract) {
    contractDiaglog = [<Typography gutterBottom variant="h7" component="h4"> Your proxy is deployed at {state.deployedAddress} </Typography>]
    proxyTitle = "You have an existing GSVE Wrapper"
    contractDiaglog.push(<Box align="center"><Button color="primary" variant="contained" target="_blank" href={contractLink} className={classes.button}> {linkText}</Button></Box>)
  }
  else {
    if(showUi === true){
      contractDiaglog = [<Box align="center"><Typography gutterBottom variant="h7" component="h4"> Burn 10 GSVE to deploy your smart wrapper</Typography>
      <Button color="primary" variant="contained" className={classes.button} onClick={handleDeployWrapper}> Deploy </Button></Box>,
      <Box align="center"><Button color="primary" variant="contained" target="_blank" href={contractLink} className={classes.button}> {linkText}</Button></Box>]
      proxyTitle = "Deploy Your Own Wrapper"
    }
    else if(showUi === false){
      contractDiaglog = [<Box align="center"><Button color="primary" variant="disabled" className={classes.button}> Login to deploy </Button></Box>]
      proxyTitle = "You Aren't Logged In"
    }
  }

  if(chainName === ""){
    if(state.chainId === 1337){
      setChainName("Development")
    }
    else if(state.chainId === 1){
      setChainName("Mainnet")
    }
    else if(state.chainId === 3){
      setChainName("Ropsten")
    }
    else if(state.chainId === 4){
      setChainName("Rinkeby")
    }
  }
  
  return (
    <div className={classes.rootPage}>
      <Paper className={classes.paper} component="p">
        <Paper className={classes.headerPaper} component="p">
          <Typography gutterBottom variant="h4" component="h4">
            Gas Save Wrapper
            <Box textAlign='right'>
              <Button color="secondary" variant="contained" href="https://docs.gassave.org">
                Find Out More
                </Button>
            </Box>
          </Typography>
          <Typography align="left" gutterBottom variant="h6" component="h4">
            GSVE Transaction Wrapper is a simple gas saving smart wallet.
          </Typography>
          <Typography align="left" gutterBottom variant="h7" component="h4">
            You can send your transaction through the wallet, using wallet connect, to save on gas fees.
          </Typography>
          <Typography align="left" gutterBottom variant="h7" component="h4">
            Burning gas tokens as part of the transaction saves up to 50% of the gas fee.
          </Typography>
        </Paper>

        <Grid container spacing={3} >
          <Grid item xs={12}>
            <Paper className={classes.headerPaper}>
              <Card className={classes.card} variant="outlined">
                <CardContent>
                  <Typography gutterBottom variant="h6" component="h4">
                    {proxyTitle}
                  </Typography>
                  <Divider light />
                  {contractDiaglog}
                </CardContent>
              </Card>
            </Paper>
          </Grid>

          {( state.hasDeployedContract ===  true) && (
          <Grid item xs={12}>
            <Paper className={classes.headerPaper}>
              <Card className={classes.card} variant="outlined">
                <Typography gutterBottom variant="h6" component="h4"> Manage Wrapper Contract </Typography>
                <CardContent>

                  <Accordion expanded={expanded === 'panel1'} onChange={handleChange('panel1')}>
                    <AccordionSummary
                      expandIcon={<ExpandMoreIcon />}
                      aria-controls="panel1bh-content"
                      id="panel21h-header"
                    >
                      <Typography gutterBottom variant="h7" component="h4"> Withdraw Wrapper Contract Balance</Typography>
                    </AccordionSummary>
                    <AccordionDetails>
                      <Grid item xs={12}>
                        <Paper>
                          <Box align="center">
                            <Typography  gutterBottom variant="h7" component="h4">  Your wrapper has a balance of: {wrapperBalance} Eth  </Typography>
                            <Button type="submit" color="primary" variant="contained" className={classes.button} onClick={handleWithdraw}> Withdraw Eth balance  </Button>
                          </Box>
                        </Paper>
                      </Grid>
                    </AccordionDetails>
                  </Accordion>

                  <Accordion expanded={expanded === 'panel2'} onChange={handleChange('panel2')}>
                    <AccordionSummary
                      expandIcon={<ExpandMoreIcon />}
                      aria-controls="panel2bh-content"
                      id="panel2bh-header"
                    >
                      <Typography gutterBottom variant="h7" component="h4"> Withdraw Token Balance</Typography>
                    </AccordionSummary>
                    <AccordionDetails>
                      <Grid item xs={12}>
                        <Paper>
                          <Box align="center">
                          <Typography gutterBottom variant="h7" component="h4">  Withdraw Wrapper Contract Token Balance </Typography>
                            <TextField className={classes.textField}
                              label="Contract Address"
                              value={contractTokenAddress}
                              style={{ width: 300, color: "white", background: "white" }}
                              inputProps={{
                                autoComplete: 'new-password', // disable autocomplete and autofill
                              }}
                              onChange={handleTokenWithdrawAddress}
                            />
                            <br />
                            <Button type="submit" color="primary" variant="contained" className={classes.button} onClick={handleTokenWithdraw}> Withdraw Tokens  </Button>
                          </Box>
                        </Paper>
                      </Grid>
                    </AccordionDetails>
                  </Accordion>
                  <Accordion expanded={expanded === 'panel3'} onChange={handleChange('panel3')}>
                    <AccordionSummary
                      expandIcon={<ExpandMoreIcon />}
                      aria-controls="panel3bh-content"
                      id="panel3bh-header"
                    >
                      <Typography gutterBottom variant="h7" component="h4"> Manual Transaction</Typography>
                    </AccordionSummary>
                    <AccordionDetails>
                      <Grid item xs={12}>
                        <Paper>
                          <Box align="center">
                          <Typography gutterBottom variant="h7" component="h4"> Send Transaction Manually Using Call Data </Typography>
                            <form onSubmit={e => { handleSubmit(e) }}>
                              <Autocomplete
                                id="country-select-demo"
                                style={{ width: 400, color: "white", background: "white" }}
                                options={upgradedTokens}
                                classes={{
                                  option: classes.option,
                                }}
                                onChange={handleTokenChange}
                                autoHighlight
                                getOptionLabel={(option) => option.label}
                                renderOption={(option) => (
                                  <React.Fragment>
                                    {option.code} - {option.label}
                                  </React.Fragment>
                                )}
                                renderInput={(params) => (
                                  <TextField
                                    value={selectedToken}
                                    {...params}
                                    label="Gas Token"
                                    variant="filled"
                                    name="selectedToken"
                                    inputProps={{
                                      ...params.inputProps,
                                      autoComplete: 'new-password', // disable autocomplete and autofill

                                    }}
                                  />
                                )}
                              />
                              <br />
                              <TextField className={classes.textField}
                                label="Contract Address"
                                value={contractAddress}
                                style={{ width: 300, color: "white", background: "white" }}
                                inputProps={{
                                  autoComplete: 'new-password', // disable autocomplete and autofill
                                }}
                                onChange={handleAddress}
                              />
                              <br />
                              <TextField className={classes.textField}
                                label="Call Data"
                                value={callData}
                                style={{ width: 300, color: "white", background: "white" }}
                                inputProps={{
                                  autoComplete: 'new-password', // disable autocomplete and autofill
                                }}
                                onChange={handleCallData}
                              />
                              <br />
                              <TextField className={classes.textField}
                                label="Transaction Value"
                                value={msgValue}
                                type="number"
                                style={{ width: 300, color: "white", background: "white" }}
                                inputProps={{
                                  autoComplete: 'new-password',
                                  step: "any", // disable autocomplete and autofill
                                }}
                                onChange={handleMsgValue}
                              />
                              <br />
                              <Button type="submit" color="primary" variant="contained" className={classes.button}>  Send Transaction  </Button>
                            </form>
                          </Box>
                        </Paper>
                      </Grid>
                    </AccordionDetails>
                  </Accordion>
                </CardContent>
              </Card>
            </Paper>

          </Grid>
          )}

          {( state.hasDeployedContract ===  true) && (
          <Grid item xs={12}>
            <Paper className={classes.headerPaper}>
              <Card className={classes.card} variant="outlined">
                <Typography gutterBottom variant="h6" component="h4"> Connect to dApps with Wallet Connect</Typography>
                <WalletConnectPage passedAddress={state.deployedAddress} passedChainId={state.chainId} passedChainName={chainName} appState={state} passedUpgraded={upgraded}/>
              </Card>
            </Paper>
          </Grid>
          )}

        </Grid>
      </Paper>
    </div>
  );
}

const upgradedTokens = [
  { code: 'wChi', label: 'Wrapped Chi', type: "wrap" },
  { code: 'wGST2', label: 'Wrapped GST2', type: "wrap" },
  { code: 'wGST1', label: 'Wrapped GST1', type: "wrap" },
  { code: 'Chi', label: 'Chi', type: "mint" },
  { code: 'GST2', label: 'Gas Token 2', type: "mint" },
  { code: 'GST1', label: 'Gas Token 1', type: "mint" },
];