import React, { useContext } from 'react';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import ImgMediaCard from "./components/Card.js";
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import Box from '@material-ui/core/Box';
import GlobalStyles from './../../GlobalStyles';
import { useTheme } from '@material-ui/core/styles';
import { Context } from './../../store'
var contractAddresses = require('./../../utils/addresses.json');

export default function Mint() {
  const classes = GlobalStyles();
  const [state, dispatch] = useContext(Context);
  return (
    <div className={classes.rootPage}>
      <Paper className={classes.paper} component="p">

        <Grid container spacing={1}  >
          <Grid item xs={12}>
            <div className="drawerPaper">
              <Paper className={classes.headerPaper}>
                <Typography gutterBottom variant="h4" component="h4">
                  Gas Station
                 <Box textAlign='right'>
                    <Button color="secondary" variant="contained" href="https://docs.gassave.org">
                      Find Out More
                    </Button>
                  </Box>
                </Typography>
                <Typography align="left" gutterBottom variant="h6" component="h4">
                  Mint Gas Tokens When Gas Fees are low
              </Typography>
                <Typography align="left" gutterBottom variant="h6" component="h4">
                  Burn Gas Tokens for a Rebate when fees are high
              </Typography>
                <Typography align="left" gutterBottom variant="h6" component="h4">
                  Claim Gas Tokens using your GSVE
              </Typography>
                <br />
              </Paper>
            </div>
          </Grid>
          <Grid item xs={12} sm={6} md={4} xl={3}>
            <ImgMediaCard tokenName="Wrapped Chi"
              tokenDescription="wChi is a wrapped version of the Chi gastoken. It is wrapped for compatability with the free-tier Gas Save Protocol"
              tokenImage="wchi"
              mintable={false}
              claimable={true}
              protocolMintFee="3"
              protocolMintReward="Rewards Disabled"
              tokenSymbol="wChi"
              claimBurn="0.1"
              gasStored="24000"
              tokenAddress={contractAddresses[state.chainId]['wchi']} />
          </Grid>
          <Grid item xs={12} sm={6} md={4} xl={3}>
            <ImgMediaCard tokenName="Chi"
              tokenDescription="Chi is a gas token by 1inch. It is only meant for use with the 1inch platform. This token must be wrapped to work with Gas Save."
              tokenImage="chi"
              mintable={true}
              claimable={true}
              protocolMintFee="2"
              protocolMintReward="Rewards Disabled"
              tokenSymbol="Chi"
              claimBurn="0.1"
              gasStored="24000"
              tokenAddress={contractAddresses[state.chainId]['chi']} />
          </Grid>
          <Grid item xs={12} sm={6} md={4} xl={3}>
            <ImgMediaCard tokenName="Wrapped GST2"
              tokenDescription="wGST2 is a wrapped version of the GST2 gastoken. It is wrapped for compatability with the free-tier Gas Save Protocol"
              tokenImage="wgst2"
              mintable={false}
              claimable={true}
              protocolMintFee="3"
              protocolMintReward="Rewards Disabled"
              tokenSymbol="wGST2"
              claimBurn="0.1"
              gasStored="24000"
              tokenAddress={contractAddresses[state.chainId]['wgst2']} />
          </Grid>
          <Grid item xs={12} sm={6} md={4} xl={3}>
            <ImgMediaCard tokenName="GST2"
              tokenDescription="GST2 is the first gas token that used child contracts to store gas. It is more efficient that GST1. It was originally aimed at developers. This token must be wrapped to work with Gas Save."
              tokenImage="gst2"
              mintable={true}
              claimable={true}
              protocolMintFee="0.02"
              protocolMintReward="Rewards Disabled"
              tokenSymbol="GST2"
              claimBurn="0.1"
              gasStored="24000"
              tokenAddress={contractAddresses[state.chainId]['gst2']} />
          </Grid>
          <Grid item xs={12} sm={6} md={4} xl={3}>
            <ImgMediaCard tokenName="Wrapped GST1"
              tokenDescription="wGST1 is a wrapped version of the GST1 gastoken. It is wrapped for compatability with the free-tier Gas Save Protocol"
              tokenImage="wgst1"
              mintable={false}
              claimable={true}
              protocolMintFee="3"
              protocolMintReward="Rewards Disabled"
              tokenSymbol="wGST1"
              claimBurn="0.1"
              gasStored="15000"
              tokenAddress={contractAddresses[state.chainId]['wgst1']} />
          </Grid>
          <Grid item xs={12} sm={6} md={4} xl={3}>
            <ImgMediaCard tokenName="GST1"
              tokenDescription="GST1 is the original gas token and has been around since 2017. It was originally aimed as a way for developers to save fees. This token must be wrapped to work with Gas Save."
              tokenImage="gst1"
              mintable={true}
              claimable={true}
              protocolMintFee="0.02"
              protocolMintReward="Rewards Disabled"
              tokenSymbol="GST1"
              claimBurn="0.1"
              gasStored="15000"
              tokenAddress={contractAddresses[state.chainId]['gst1']} />
          </Grid>
        </Grid>
      </Paper>
    </div>
  );
}
