import React, { useContext } from 'react';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import clsx from 'clsx';
import Collapse from '@material-ui/core/Collapse';
import IconButton from '@material-ui/core/IconButton';
import Divider from '@material-ui/core/Divider';
import Paper from '@material-ui/core/Paper';
import GlobalStyles from './../../../GlobalStyles';
import { useTheme } from '@material-ui/core/styles';
import { Context } from './../../../store'
import { feePoolBalance } from "./../../../utils/web3_transaction_maker.js"

export default function ImgMediaCard(props) {
  const classes = GlobalStyles();
  const [state, dispatch] = useContext(Context);
  const [amountAvailable, setAmountAvailable] = React.useState(-1);
  var tokenName = props.tokenName;
  var tokenImage = props.tokenImage;
  var tokenSymbol = props.tokenSymbol;

  if (state.connected && state.account !== "0x00") {
    if (amountAvailable === -1) {
      feePoolBalance(state.web3_caller, tokenName, state.chainId).then((value) => { setAmountAvailable(value) });
    }
  }

  return (
    <Paper className={classes.headerPaper}>
      <Card className={classes.root} variant="outlined">
        <CardContent>
          <Grid container spacing={2}>
            <Grid item xs={12}>
              <Typography gutterBottom variant="h5" component="h2">
                {tokenName}
              </Typography>
            </Grid>

            <Grid item xs={4}> </Grid>
            <Grid item xs={4}>
              <CardMedia
                component="img"
                height="80"
                objectFit='contain'
                image={"images/tokens/" + tokenImage + ".png"}
              />
            </Grid>
            <Grid item xs={4}> </Grid>

            <Grid item xs={12}>
              <Typography variant="body2" color="textSecondary" component="p">
                {amountAvailable + " " + tokenSymbol + " Available"}
              </Typography>
            </Grid>
          </Grid>
        </CardContent>
      </Card>
    </Paper>
  );
}


