import React from 'react';
import './App.css';
import Store from './store'
import MiniDrawer from "./components/appbar.js"
import { createMuiTheme, ThemeProvider } from '@material-ui/core/styles';
import { green, orange, black } from '@material-ui/core/colors';

const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#3949ab',
    },
    secondary: {
      main: '#01579b',
    },
  },
});

function App() {
  return (
    <ThemeProvider theme={theme}>
      <Store>
        <div className="App">
          < MiniDrawer />
        </div>
      </Store>
    </ThemeProvider>
  );
}
export default App;
