import React, { useContext } from 'react';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import Box from '@material-ui/core/Box';
import Grid from '@material-ui/core/Grid';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Autocomplete from '@material-ui/lab/Autocomplete';
import Divider from '@material-ui/core/Divider';
import Slider from '@material-ui/core/Slider';
import SyncIcon from '@material-ui/icons/Sync';
import IconButton from '@material-ui/core/IconButton';
import Input from '@material-ui/core/Input';
import GlobalStyles from './../../GlobalStyles';
import ImgMediaCard from "./components/Card.js";
import { useTheme } from '@material-ui/core/styles';

import { userStakeSize, feePoolBalance, handleClaim, getClaimTime } from "./../../utils/web3_transaction_maker.js"
import { Context } from './../../store'
import { findAllByDisplayValue } from '@testing-library/dom';


export default function Claim() {
  const classes = GlobalStyles();
  const [selectedToken, setSelectedToken] = React.useState("");
  const [selectedSymbol, setSelectedSymbol] = React.useState("");
  const [userStake, setUserStake] = React.useState(-1);
  const [userTier, setUserTier] = React.useState("");
  const [sliderValue, setSliderValue] = React.useState(1);
  const [claimTime, setClaimTime] = React.useState("");
  const [showClaimUi, setShowClaimUi] = React.useState("");

  const [state, dispatch] = useContext(Context);

  const handleSliderChange = (event, newValue) => {
    setSliderValue(newValue);
  };

  const handleInputChange = (event) => {
    setSliderValue(event.target.value === '' ? '' : Number(event.target.value));
  };

  const handleBlur = () => {
    if (sliderValue < 0) {
      setSliderValue(0);
    } else if (sliderValue > 100) {
      setSliderValue(100);
    }

  };

  const handleSubmit = (e) => {
    e.preventDefault();
    handleClaim(state.web3, sliderValue, selectedToken, state.chainId)
  }

  const handleTokenChange = (e) => {
    try {
      setSelectedToken(e.target.innerText.split("-")[1])
      setSelectedSymbol(e.target.innerText.split("-")[0])
    }
    catch {
      setSelectedToken("")
      setSelectedSymbol("")
    }
  };

  
  var showUi = false
  var login = []

  if (state.connected && state.account !== "0x00") {
    showUi = true
    if (userStake === -1) {
      userStakeSize(state.web3_caller, state.account, state.chainId).then((value) => { setUserStake(value) });
      login = []
    }
  }
  else {
    showUi = false
    login = [<Typography align="left" gutterBottom variant="h8" component="h4"> Login to claim tokens</Typography>]
    if (userStake !== -1) {
      setUserStake(-1)
      setUserTier("")
    }
  }

  if (userTier === "" && userStake !== -1) {
    if (userStake >= 1000) {
      if(claimTime === ""){
        getClaimTime(state.web3_caller, state.chainId).then((value) => { setClaimTime(value) });
      }
      else{
        setShowClaimUi(true)
        setUserTier([<Typography align="left" gutterBottom variant="h8" component="h4"> You are a tier 2 Staker.</Typography>,
        <Typography align="left" gutterBottom variant="h8" component="h4"> You can claim gas tokens from the fee pool</Typography>,
        <Typography align="left" gutterBottom variant="h8" component="h4"> {claimTime}</Typography>])
      }
    }
    else if (userStake >= 250) {
      setShowClaimUi(false)
      setUserTier([<Typography align="left" gutterBottom variant="h8" component="h4"> You are a tier 1 Staker.</Typography>,
      <Typography align="left" gutterBottom variant="h8" component="h4"> Stake {1000 - userStake} more to reach tier 2. This will let you claim from the gas token fee pool.</Typography>])
    }
    else {
      setShowClaimUi(false)
      setUserTier([<Typography align="left" gutterBottom variant="h8" component="h4"> You are a tier 0 Staker.</Typography>,
      <Typography align="left" gutterBottom variant="h8" component="h4">  Stake {1000 - userStake} more to reach tier 2. This will let you claim from the gas token fee pool.</Typography>])
    }
  }

  var summary

  if (showClaimUi) {
    var claimAmount = sliderValue
    if (selectedSymbol.includes("GST")) {
      claimAmount = claimAmount / 100
    }
    summary = [<Typography gutterBottom variant="h6" component="h4"> Summary  </Typography>, <Typography gutterBottom align="left" variant="h8" component="h4">{"You will claim " + claimAmount.toString() + " " + selectedSymbol + " by burning  " + (sliderValue * 0.1).toString().slice(0, 3) + " GSVE"}</Typography>]
  }

  var submitButton;
  if (showClaimUi) {
    submitButton = [<Button type="submit" variant="contained" color="primary" className={classes.button} > Submit </Button>]
  }
  else {
    submitButton = [<Button variant="disabled" color="primary" className={classes.button} > Stake more GSVE to claim </Button>]
  }


  return (
    <div className={classes.rootPage}>
      <Paper className={classes.paper} component="p">
        <Grid container spacing={2} >
          <Grid item xs={12}>
            <div className="drawerPaper">
              <Paper className={classes.headerPaper}>
                <Typography gutterBottom variant="h4" component="h4">
                  Claim Gas Tokens
                  <Box textAlign='right'>
                    <Button color="secondary" variant="contained" href="https://docs.gassave.org">
                      Find Out More
                      </Button>
                  </Box>
                </Typography>
                <Typography align="left" gutterBottom variant="h6" component="h4">
                  Stake 1,000 GSVE to become tier 2
                </Typography>
                <Typography align="left" gutterBottom variant="h6" component="h4">
                  Burn GSVE to take tokens from the Fee Pool
                </Typography>
                <br />
              </Paper>
            </div>
          </Grid>
          <Grid item xs={12}>
            <Card className={classes.headerPaper} variant="outlined">
              <Paper>
                <CardContent>
                  <Typography gutterBottom variant="h5" component="h4">
                    Tokens Available
                  </Typography>
                  <Divider light />
                  {userTier}
                  <Divider light />
                </CardContent>

                <Grid container spacing={1}  >
                  <Grid item xs={12} sm={6} md={2} xl={2}>
                    <ImgMediaCard tokenName="Wrapped Chi"
                      tokenImage="wchi"
                      tokenSymbol="wChi" />
                  </Grid>
                  <Grid item xs={12} sm={6} md={2} xl={2}>
                    <ImgMediaCard tokenName="Wrapped GST2"
                      tokenImage="wGST2"
                      tokenSymbol="wGST2" />
                  </Grid>
                  <Grid item xs={12} sm={6} md={2} xl={2}>
                    <ImgMediaCard tokenName="Wrapped GST1"
                      tokenImage="wGST1"
                      tokenSymbol="wGST1" />
                  </Grid>
                  <Grid item xs={12} sm={6} md={2} xl={2}>
                    <ImgMediaCard tokenName="Chi"
                      tokenImage="chi"
                      tokenSymbol="Chi" />
                  </Grid>
                  <Grid item xs={12} sm={6} md={2} xl={2}>
                    <ImgMediaCard tokenName="Gas Token 2"
                      tokenImage="GST2"
                      tokenSymbol="GST2" />
                  </Grid>
                  <Grid item xs={12} sm={6} md={2} xl={2}>
                    <ImgMediaCard tokenName="Gas Token 1"
                      tokenImage="GST1"
                      tokenSymbol="GST1" />
                  </Grid>
                </Grid>
              </Paper>
            </Card>
          </Grid>
          {showUi && (
            <Grid container spacing={1} >
              <Grid item xs={12} sm={12} md={12} xl={4}> </Grid>
              <Grid item xs={12} sm={12} md={12} xl={4}>
                <Paper className={classes.headerPaper}>
                  <Card className={classes.card} variant="outlined">
                    <form onSubmit={e => { handleSubmit(e) }}>
                      <CardContent>
                        <Typography gutterBottom variant="h6" component="h4">
                          Claim Tokens
                  </Typography>
                        <Box align="center">
                          <Autocomplete
                            id="country-select-demo"
                            style={{ width: 400, color: "white", background: "white" }}
                            options={tokens}
                            classes={{
                              option: classes.option,
                            }}
                            onChange={handleTokenChange}
                            autoHighlight
                            getOptionLabel={(option) => option.label}
                            renderOption={(option) => (
                              <React.Fragment>
                                {option.code} - {option.label}
                              </React.Fragment>
                            )}
                            renderInput={(params) => (
                              <TextField
                                {...params}
                                label="Gas Token"
                                variant="filled"
                                name="selectedToken"
                                inputProps={{
                                  ...params.inputProps,
                                  autoComplete: 'new-password', // disable autocomplete and autofill

                                }}
                              />
                            )}
                          />
                        </Box>
                        <br />
                        <br />
                        <Typography align="left" gutterBottom variant="h8" component="h4">
                          Number of tokens:
                  </Typography>
                        <Box align="center">
                          <Slider
                            className={classes.inputSlider}
                            defaultValue={1}
                            min={1}
                            max={5}
                            aria-labelledby="input-slider"
                            step={1}
                            onChange={handleSliderChange}
                            value={typeof sliderValue === 'number' ? sliderValue : 0}
                          />
                          <Input
                            value={sliderValue}
                            margin="dense"
                            className={classes.input}
                            onChange={handleInputChange}
                            onBlur={handleBlur}
                            inputProps={{
                              step: 1,
                              min: 1,
                              max: 5,
                              type: 'number',
                              'aria-labelledby': 'input-slider',
                            }}
                          />
                        </Box>
                        <Paper variant="outlined">

                          {summary}
                        </Paper >
                        <br />
                        {submitButton}
                      </CardContent>
                    </form>
                  </Card>
                </Paper>
              </Grid>
            </Grid>
          )}

        </Grid>
      </Paper>
    </div>
  );
}
const tokens = [
  { code: 'wChi', label: 'Wrapped Chi', type: "wrap" },
  { code: 'wGST2', label: 'Wrapped GST2', type: "wrap" },
  { code: 'wGST1', label: 'Wrapped GST1', type: "wrap" },
  { code: 'Chi', label: 'Chi', type: "mint" },
  { code: 'GST2', label: 'Gas Token 2', type: "mint" },
  { code: 'GST1', label: 'Gas Token 1', type: "mint" },
];