import React, { useContext } from 'react';
import clsx from 'clsx';
import Drawer from '@material-ui/core/Drawer';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import List from '@material-ui/core/List';
import CssBaseline from '@material-ui/core/CssBaseline';
import Typography from '@material-ui/core/Typography';
import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Link from '@material-ui/core/Link';
import Button from '@material-ui/core/Button';
import MenuItem from '@material-ui/core/MenuItem';
import FormHelperText from '@material-ui/core/FormHelperText';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import InputLabel from '@material-ui/core/InputLabel';
import Alert from '@material-ui/lab/Alert';

import WhatshotIcon from '@material-ui/icons/Whatshot';
import AccountBalanceIcon from '@material-ui/icons/AccountBalance';
import LocalGasStationIcon from '@material-ui/icons/LocalGasStation';
import InvertColorsIcon from '@material-ui/icons/InvertColors';
import HomeIcon from '@material-ui/icons/Home';
import AccountBalanceWalletIcon from '@material-ui/icons/AccountBalanceWallet';
import ExitToAppIcon from '@material-ui/icons/ExitToApp';
import NoteAddIcon from '@material-ui/icons/NoteAdd';
import GrainIcon from '@material-ui/icons/Grain';
import SubjectIcon from '@material-ui/icons/Subject';
import DescriptionIcon from '@material-ui/icons/Description';
import DescriptionTwoToneIcon from '@material-ui/icons/DescriptionTwoTone';
import SyncIcon from '@material-ui/icons/Sync';
import FireplaceIcon from '@material-ui/icons/Fireplace';

import { connect, disconnect, getAccount, getWeb3Caller, getChainID } from "./../utils/web3_handler.js"

import { Switch, Route } from "react-router-dom";
import Stake from "./../pages/Stake/Stake.js"
import Station from "./../pages/Station/Station.js"
import Deployer from "./../pages/Deployer/Deployer.js"
import Saver from "./../pages/Saver/Saver.js"
import Gnosis from "./../pages/Gnosis/Gnosis.js"
import Home from '../pages/Home/Home.js';
import Mint from "./../pages/Mint/Mint.js"
import Claim from "./../pages/Claim/Claim.js"
import Litepaper from "../pages/Litepaper/Litepaper.js"
import WalletConnectPage from "./../pages/WalletConnectPage/WalletConnectPage.tsx"

import GasSaveLogo from './../logo.svg';
import GasSaveLogoGrey from './../logoGrey.svg';
import { Context } from './../store'
import GlobalStyles from './../GlobalStyles';
import { useTheme } from '@material-ui/core/styles';

export default function MiniDrawer() {
  const classes = GlobalStyles();
  const theme = useTheme();
  const [open, setOpen] = React.useState(true);
  const [network, setNetwork] = React.useState(1);
  const [walletChainId, setWalletChainId] = React.useState(0);
  var gasSavePrice = "$0.01"

  const [state, dispatch] = useContext(Context);

  const handleDrawerOpen = () => {
    setOpen(true);
    checkConnection()
  };

  const handleDrawerClose = () => {
    setOpen(false);
    checkConnection()
  };

  const handleConnection = () => {
    var web3 = connect()
    web3.then((value) => { dispatch({ type: 'web3_connected', payload: value }) });
    web3.then((value) => { dispatch({ type: 'web3_caller_connected', payload: value }) });
    dispatch({ type: 'set_connected', payload: true })
    checkConnection()
  }

  const handleDisonnection = () => {
    dispatch({ type: 'web3_disconnected', payload: "" })
    disconnect()
  }

  const checkConnection = () => {
    if (state.web3 !== null) {
      if (state.account === "0x00") {
        var account = getAccount(state.web3)
        account.then((value) => { dispatch({ type: 'set_account', payload: value }) });
      }
    }
  }

  const connectCaller = () => {
    if (state.web3_caller === null) {
      dispatch({ type: 'web3_caller_connected', payload: getWeb3Caller(state.chainId) })
    }
  }

  const updateCaller = () => {
    dispatch({ type: 'web3_caller_connected', payload: getWeb3Caller(state.chainId) })
  }

  const updateChainId = () => {
    if(state.connected && state.account !=="0x00"){
      getChainID(state.web3).then((value) => { setWalletChainId(value) })
    }
  }

  updateChainId()
  connectCaller()

  const handleNetworkChange = (event) => {
    dispatch({ type: 'chainId', payload: event.target.value })
    setNetwork(event.target.value);
    updateChainId()
    updateCaller()
  };

  var connectbutton = []

  if(state.connected !== true){
  connectbutton.push(<><FormControl className={classes.formControl}>
                    <Select
                      labelId="demo-simple-select-label"
                      id="demo-simple-select"
                      value={network}
                      defaultValue={1}
                      onChange={handleNetworkChange}
                      classes={{
                        root: classes.headerPaper,
                      }} 
                    >
                      <MenuItem value={1}>Network: Ethereum</MenuItem>
                      <MenuItem value={4}>Network: Rinkeby</MenuItem>
                    </Select>
                  </FormControl>
                  </> )
                  }

  if (!state.connected) {
    connectbutton.push(<Button color="inherit" onClick={() => [handleConnection()]}> <AccountBalanceWalletIcon />  Connect To Wallet</Button>)
  }
  else {
    if (state.account === "0x00") {
      connectbutton.push(<IconButton color="inherit" aria-label="open drawer" onClick={checkConnection}><AccountBalanceWalletIcon /><SyncIcon /></IconButton>)
    }
    else {
      connectbutton.push(<Typography variant="h10" noWrap> <AccountBalanceWalletIcon /> {state.account} </Typography>)
    }
    connectbutton.push(<Button color="inherit" onClick={() => [handleDisonnection()]}> <ExitToAppIcon /> Disconnect </Button>)
  }

  const menuText = ['Home', 'Stake', 'Gas Station', "Claim Gas Tokens", "Mint & Wrap Gas"]
  const menuLinks = ['#/', '#/stake', '#/station', "#/claim", "#/mint"]
  const menuIcons = [<HomeIcon />, <AccountBalanceIcon />, <LocalGasStationIcon />, <GrainIcon />, <InvertColorsIcon />]

  const middleText = ["GSVE Deployer", "GSVE Smart Wrapper", "GSVE Gnosis"]
  const middleLinks = ["#/deployer", "#/saver", "#/gnosis"]
  const middleIcons = [<NoteAddIcon />, <WhatshotIcon />, <FireplaceIcon />,]

  const bottomText = ["Litepaper", "Whitepaper", "Docs",]// ("GSVE Eth: " + gasSavePrice )]
  const bottomLinks = ["#/litepaper", "https://docs.gassave.org/whitepaper/", "https://docs.gassave.org",]// "https://app.uniswap.org/#/swap?outputCurrency="]
  const bottomIcons = [<DescriptionTwoToneIcon />, <DescriptionIcon />, <SubjectIcon />]//, <img src={GasSaveLogoGrey} alt="" width="32" />]

//https://api.etherscan.io/api?module=gastracker&action=gasoracle&apikey=YourApiKeyToken

  var alert = []
  var networkName;
  if (state.connected && state.account === "0x00") {
    checkConnection()
  }

  if(state.chainId !== walletChainId && state.account !== "0x00"){
    if(state.connected){
      alert = [<Alert severity="warning">The network selected is not the same as the wallet network. Disconnect, change network and then log back in.</Alert>]
    }
    if (state.chainId === 1){
      networkName = " Ethereum"
    }
    if (state.chainId === 3){
      networkName = " Ethereum Ropsten"
    }
    if (state.chainId === 4){
      networkName = " Ethereum Rinkeby"
    }
    if (state.chainId === 1337){
      networkName = " Development"
    }
  }

  return (
    <div className={classes.root}>
      <CssBaseline />
      <AppBar
        position="fixed"
        className={clsx(classes.appBar, {
          [classes.appBarShift]: open,
        })}
      >
        <Toolbar>
          <IconButton
            color="inherit"
            aria-label="open drawer"
            onClick={handleDrawerOpen}
            edge="start"
            className={clsx(classes.menuButton, {
              [classes.hide]: open,
            })}
          >
            <MenuIcon />
          </IconButton>
          <Typography variant="h5" noWrap>
            <img src={GasSaveLogo} alt="" /> Gas Save  {networkName}
          </Typography> 
          <div className={classes.toolbarButtons}>
            {connectbutton}
          </div>
          
        </Toolbar>
        {alert}
      </AppBar>
      <Drawer
        variant="permanent"
        className={clsx(classes.drawer, {
          [classes.drawerOpen]: open,
          [classes.drawerClose]: !open,
        })}
        classes={{
          paper: clsx({
            [classes.drawerOpen]: open,
            [classes.drawerClose]: !open,
          }),
        }}
      >
        <div className={classes.toolbar}>
          <IconButton onClick={handleDrawerClose}>
            {theme.direction === 'rtl' ? <ChevronRightIcon /> : <ChevronLeftIcon />}
          </IconButton>
        </div>
        <Divider />
        <List>
          {menuText.map((text, index) => (
              <ListItem button component="a" href={menuLinks[index]}>
              <Link href={menuLinks[index]}>
                <ListItemIcon>{menuIcons[index]}</ListItemIcon>
              </Link>
              <Link href={menuLinks[index]}>
                <ListItemText primary={text} />
              </Link>
            </ListItem>
          ))}
        </List>

        <Divider light />
        <List>
          {middleText.map((text, index) => (
            <ListItem button component="a" href={middleLinks[index]}>
              <Link href={middleLinks[index]}>
                <ListItemIcon>{middleIcons[index]}</ListItemIcon>
              </Link>
              <Link href={middleLinks[index]}>
                <ListItemText primary={text} />
              </Link>
            </ListItem>
          ))}
        </List>
        <Divider />
        <Divider light />

        <List>
          {bottomText.map((text, index) => (
            <ListItem button component="a" href={bottomLinks[index]}>
              <Link href={bottomLinks[index]}>
                <ListItemIcon>{bottomIcons[index]}</ListItemIcon>
              </Link>
              <Link href={bottomLinks[index]}>
                <ListItemText primary={text} />
              </Link>
            </ListItem>
          ))}
        </List>
        <Divider />

      </Drawer>
      <main className={classes.content}>
        <Switch>
          <Route path="/stake">
            <Stake />
          </Route>
          <Route path="/station">
            <Station />
          </Route>
          <Route path="/deployer">
            <Deployer />
          </Route>
          <Route path="/mint">
            <Mint />
          </Route>
          <Route path="/claim">
            <Claim />
          </Route>
          <Route path="/saver">
            <Saver />
          </Route>
          <Route path="/gnosis">
            <Gnosis />
          </Route>
          <Route path="/litepaper">
            <Litepaper />  
          </Route> 
          <Route path="/">
            <Home />
          </Route>
        </Switch>
      </main>
    </div>
    
  );
}
