import React, { useContext } from 'react';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import Box from '@material-ui/core/Box';
import Grid from '@material-ui/core/Grid';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Autocomplete from '@material-ui/lab/Autocomplete';
import Divider from '@material-ui/core/Divider';

import GlobalStyles from './../../GlobalStyles';
import { useTheme } from '@material-ui/core/styles';

import ExpandMoreIcon from '@material-ui/icons/ExpandMore';

import WalletConnectPage from "./../WalletConnectPage/WalletConnectPage.tsx"

import { gnosisSafeAddress, deployGsveGnosis, updateGnosisGasToken, getCurrentGnosisToken, approveGnosisSafeSpending} from "./../../utils/web3_transaction_maker.js"
import { Context } from './../../store'
var contractAddresses = require('./../../utils/addresses.json');

export default function Gnosis() {
  const classes = GlobalStyles();
  const [msgValue, setMsgValue] = React.useState(0);
  const [contractAddress, setContractAddress] = React.useState("");
  const [selectedToken, setSelectedToken] = React.useState("");
  const [selectedSymbol, setSelectedSymbol] = React.useState("");
  const [currentGasToken, setCurrentGasToken] = React.useState("");
  const [gnosisAddress, setGnosisAddress] = React.useState("");
  const [hasGnosisAddress, setHasGnosisAddress] = React.useState("");
  const [state, dispatch] = useContext(Context);


  const handleSubmit = (e) => {
    e.preventDefault();
    updateGnosisGasToken(state.web3, selectedToken, gnosisAddress, state.chainId);
  }

  const handleApproveSubmit = (e) => {
    e.preventDefault();
    approveGnosisSafeSpending(state.web3, state.chainId, gnosisAddress);
  }

  const handleDeployWrapper = (e) => {
    e.preventDefault();
    deployGsveGnosis(state.web3, state.chainId);
  }

  const handleTokenChange = (e) => {
    try {
      setSelectedToken(e.target.innerText.split("-")[1])
      setSelectedSymbol(e.target.innerText.split("-")[0])
    }
    catch {
      setSelectedToken("")
      setSelectedSymbol("")
    }
  };
  
  var showUi = false

  if (state.connected && state.account !== "0x00") {
    showUi = true
    if (gnosisAddress === "" ) {
      gnosisSafeAddress(state.web3, state.chainId).then((value) => {  setGnosisAddress(value) });
    }

    if (gnosisAddress !== "" && hasGnosisAddress === "") {
      if (gnosisAddress === "0x0000000000000000000000000000000000000000") {
        setHasGnosisAddress(false)
      }
      else if (gnosisAddress.includes("0x")) {
        setHasGnosisAddress(true)
      }
    }
    if(gnosisAddress !== "" && currentGasToken === ""){
      getCurrentGnosisToken(state.web3, state.chainId, gnosisAddress).then((value) => {  setCurrentGasToken(value) });
    }
  } else{
    showUi = false
  }

  var contractDiaglog
  var upgradeDialog = []
  var proxyTitle

  var contractLink = ""
  var linkText = ""

  if(state.chainId === 1 || state.chainId === 3 || state.chainId === 4 || state.chainId === 1337){
    linkText = "View on EtherScan"
    contractLink = "https://etherscan.io/address/"+ contractAddresses[state.chainId]['gnosis'] +"#code"
  }

  var gnosisAddressLink
  if(state.chainID === 4){
    gnosisAddressLink = "https://rinkeby.gnosis-safe.io/app/#/safes/" + gnosisAddress + "/balances"
  }
  else{
    gnosisAddressLink = "https://gnosis-safe.io/app/#/safes/" + gnosisAddress + "/balances"
  }

  
  
  if (hasGnosisAddress) {
    upgradeDialog = []
    contractDiaglog = [<Typography gutterBottom variant="h7" component="h4"> Your Safe is deployed at {gnosisAddress} </Typography>]
    proxyTitle = "You have an existing GSVE Wrapper"
    contractDiaglog.push(<Box align="center"><Button color="primary" variant="contained" target="_blank" href={contractLink} className={classes.button}> {linkText}</Button></Box>)
    contractDiaglog.push(<Box align="center"><Button color="primary" variant="contained" target="_blank" href={gnosisAddressLink} className={classes.button}>Load in Gnosis Safe</Button></Box>)
  }
  else {
    if(showUi === true){
      contractDiaglog = [<Typography gutterBottom variant="h7" component="h4"> Deploying your GSVE Gnosis Safe will burn 50 GSVE </Typography>,
                         <Typography gutterBottom variant="h7" component="h4"> {state.account} will be the only Safe Owner and there will be no modules enabled for this safe </Typography>, 
                        <Box align="center"><Button color="primary" variant="contained" className={classes.button} onClick={handleDeployWrapper}> Deploy </Button></Box>,
                        <Box align="center"><Button color="primary" variant="contained" target="_blank" href={contractLink} className={classes.button}> {linkText}</Button></Box>]
      proxyTitle = "Deploy Your GSVE Gnosis Safe"
    }
    else if(showUi === false){
      contractDiaglog = [<Box align="center"><Button color="primary" variant="disabled" className={classes.button}> Login to deploy </Button></Box>]
      proxyTitle = "You Aren't Logged In"
    }
  }
  
  return (
    <div className={classes.rootPage}>
      <Paper className={classes.paper} component="p">
        <Paper className={classes.headerPaper} component="p">
          <Typography gutterBottom variant="h4" component="h4">
            Gas Save Gnosis
            <Box textAlign='right'>
              <Button color="secondary" variant="contained" href="https://docs.gassave.org">
                Find Out More
                </Button>
            </Box>
          </Typography>
          <Typography align="left" gutterBottom variant="h6" component="h4">
            GSVE Gnosis is a Gas Saving Gnosis Safe
          </Typography>
          <Typography align="left" gutterBottom variant="h7" component="h4">
            It burns gas tokens when you execute transactions using gnosis, saving up to 50% 
          </Typography>
          <Typography align="left" gutterBottom variant="h7" component="h4">
            100% compatible with the Gnosis Safe App!
          </Typography>
        </Paper>

        <Grid container spacing={3} >
          <Grid item xs={12}>
            <Paper className={classes.headerPaper}>
              <Card className={classes.card} variant="outlined">
                <CardContent>
                  <Typography gutterBottom variant="h6" component="h4">
                    {proxyTitle}
                  </Typography>
                  <Divider light />
                  {contractDiaglog}
                </CardContent>
              </Card>
            </Paper>
          </Grid>


          {( hasGnosisAddress ===  true) && (

            <>
            <Grid item xs={12}>
            <Paper className={classes.headerPaper}>
              <Card className={classes.card} variant="outlined">
                <Paper>
                  <Box align="center">
                  <Typography gutterBottom variant="h5" component="h4"> Your Safe Is Currently {currentGasToken} </Typography>
                  <br />
                    <Button type="submit" color="primary" variant="contained" onClick={handleApproveSubmit}> Approve Safe To Burn GasTokens </Button>
                    <br />
                  </Box>
                </Paper>
              </Card>
            </Paper>

          </Grid>
          <Grid item xs={12}>
            <Paper className={classes.headerPaper}>
              <Card className={classes.card} variant="outlined">
                <Paper>
                  <Typography gutterBottom variant="h5" component="h4"> Update GasToken used by GSVE Gnosis Safe </Typography>
                  <Box align="center">
                    <form onSubmit={e => { handleSubmit(e) }}>
                      <Autocomplete
                        id="country-select-demo"
                        style={{ width: 400, color: "white", background: "white" }}
                        options={tokens}
                        classes={{
                          option: classes.option,
                        }}
                        onChange={handleTokenChange}
                        autoHighlight
                        getOptionLabel={(option) => option.label}
                        renderOption={(option) => (
                          <React.Fragment>
                            {option.code} - {option.label}
                          </React.Fragment>
                        )}
                        renderInput={(params) => (
                          <TextField
                            value={selectedToken}
                            {...params}
                            label="Gas Token"
                            variant="filled"
                            name="selectedToken"
                            inputProps={{
                              ...params.inputProps,
                              autoComplete: 'new-password', // disable autocomplete and autofill

                            }}
                          />
                        )}
                      />
                      <Button type="submit" color="primary" variant="contained" className={classes.button}> Update Gnosis GasToken </Button>
                    </form>
                  </Box>
                </Paper>
              </Card>
            </Paper>

          </Grid>
          </>
          )}


        </Grid>
      </Paper>
    </div>
  );
}

const tokens = [
  { code: 'off', label: 'Turn Off', type: "off" },
  { code: 'wChi', label: 'Wrapped Chi', type: "wrap" },
  { code: 'wGST2', label: 'Wrapped GST2', type: "wrap" },
  { code: 'wGST1', label: 'Wrapped GST1', type: "wrap" },
  { code: 'Chi', label: 'Chi', type: "mint" },
  { code: 'GST2', label: 'Gas Token 2', type: "mint" },
  { code: 'GST1', label: 'Gas Token 1', type: "mint" },
];