import React, { useContext } from 'react';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import Divider from '@material-ui/core/Divider';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import Box from '@material-ui/core/Box';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import GlobalStyles from './../../GlobalStyles';
import { useTheme } from '@material-ui/core/styles';
import {
  stakeGSVE, getTokenSupplyInfo, getTokenBalance,
  unstakeGSVE, claimRewardTokens, rewardsRemaining,
  totalStaked, userStakeSize, userTotalReward,
  userUnclaimedReward
} from "./../../utils/web3_transaction_maker.js"
import { Context } from './../../store'
import { findAllByDisplayValue } from '@testing-library/dom';


export default function Stake() {
  const classes = GlobalStyles();
  var swapLink = ("https://app.uniswap.finance/#/swap?outputCurrency=0x00")
  var stakeValue = "";

  const [state, dispatch] = useContext(Context);

  const handleStakeFieldChange = (e) => {
    stakeValue = e.target.value
    stakeValue = stakeValue.toString();
  }

  const handleStakeSubmit = (e) => {
    e.preventDefault();
    stakeGSVE(stakeValue.toString(), state.web3, state.chainId);
  }

  //call data functions
  const [tokensLeft, settokensLeft] = React.useState(-1);
  const [tokensBurned, setTokensBurned] = React.useState(-1);
  const [tokenStaked, setTokenStaked] = React.useState(-1);
  const [rewardsLeft, setrewardsLeft] = React.useState(-1);

  if (state.web3_caller !== null) {
    if (tokenStaked === -1) {
      totalStaked(state.web3_caller, state.chainId).then((value) => { setTokenStaked(value) });
    }
    if (rewardsLeft === -1) {
      rewardsRemaining(state.web3_caller, state.chainId).then((value) => { setrewardsLeft(value) });
    }
    if (tokensLeft === -1) {
      getTokenSupplyInfo("GSVE", state.web3_caller, state.chainId).then((value) => { settokensLeft(value) });
    }
    if (tokensBurned === -1) {
      if (tokensLeft !== -1) {
        setTokensBurned(100000000 - tokensLeft);
      }
    }
  }

  //user specific functions
  const [balance, setBalance] = React.useState(-1);
  const [userStake, setUserStake] = React.useState(-1);
  const [userUnclaimed, setUserUnclaimed] = React.useState(-1);
  const [userTotalClaimed, setUserTotalClaimed] = React.useState(-1);
  const [userTier, setUserTier] = React.useState("");
  var showUi = false
  if (state.connected && state.account !== "0x00") {
    showUi = true
    if (state.web3 !== null) {
      if (balance === -1) {
        getTokenBalance("GSVE", state.web3_caller, state.account, state.chainId).then((value) => { setBalance(value) });
      }
      if (userStake === -1) {
        userStakeSize(state.web3_caller, state.account, state.chainId).then((value) => { setUserStake(value) });
      }
    }
    if (userUnclaimed === -1) {
      userUnclaimedReward(state.web3_caller, state.account, state.chainId).then((value) => { setUserUnclaimed(value) });
    }
    if (userTotalClaimed === -1) {
      userTotalReward(state.web3_caller, state.account, state.chainId).then((value) => { setUserTotalClaimed(value) });
    }
    if (userTier === "" && userStake !== -1) {
      if (userStake >= 1000) {
        setUserTier([<Divider light />, <Typography gutterBottom variant="h8" component="h4"> You are a tier 2 Staker.</Typography>,
        <Divider light />, <Typography gutterBottom variant="h8" component="h4"> You can claim gas tokens from the fee pool</Typography>,
        <Divider light />, <Typography gutterBottom variant="h8" component="h4"> You have lower protocol mint fees!</Typography>])
      }
      else if (userStake >= 250) {
        setUserTier([<Divider light />, <Typography gutterBottom variant="h8" component="h4"> You are a tier 1 Staker.</Typography>,
        <Divider light />, <Typography gutterBottom variant="h8" component="h4"> Stake {1000 - userStake} more to reach tier 2. This will let you claim from the gas token fee pool.</Typography>,
        <Divider light />, <Typography gutterBottom variant="h8" component="h4"> You have reduced protocol minting fees!</Typography>])
      }
      else {
        setUserTier([<Divider light />, <Typography gutterBottom variant="h8" component="h4"> You are a tier 0 Staker.</Typography>,
        <Divider light />, <Typography gutterBottom variant="h8" component="h4">  Stake {250 - userStake} more to reach tier 1 for reduced mint fees.</Typography>,
        <Divider light />, <Typography gutterBottom variant="h8" component="h4">  Stake {1000 - userStake} more to reach tier 2. This will let you claim from the gas token fee pool.</Typography>])
      }
    }
  }
  else {
    showUi = false
  }

  const handleUnStake = (e) => {
    unstakeGSVE(state.web3, state.chainId)
  }

  const handleRewardClaim = (e) => {
    claimRewardTokens(state.web3, state.chainId)
  }

  /*
  var showClaim = false;
  var claim;
  if (showClaim) {
    claim = [
      <Grid item xs={12} sm={6} md={4} xl={4}>
        <Paper className={classes.headerPaper}>
          <Card className={classes.card} variant="outlined">
            <CardContent>
              <Typography gutterBottom variant="h6" component="h4">
                Your GSVE Stake Reward
      </Typography>
              <Divider light />
              <Typography gutterBottom variant="h7" component="h4">
                You have an unclaimed GSVE Balance of : {userUnclaimed}
              </Typography>
              <Divider light />

              <br />
              <Button variant="contained" color="primary" onClick={handleRewardClaim} className={classes.button}> Claim Your Rewards </Button>
              {false & (<Button variant="contained" color="primary" target="_blank" href={swapLink} className={classes.button}> Trade on Uniswap </Button>)}
              <br />
              <br />
            </CardContent>
          </Card>
        </Paper>
      </Grid>]
  }
  claimTier = [<Typography gutterBottom variant="h7" component="h4"> In total you have claimed : {userTotalClaimed} GSVE </Typography>]
  */
  return (
    <div className={classes.rootPage}>
      <Paper className={classes.paper} component="p">
        <Grid container spacing={1} >
          <Grid item xs={12}>
            <div className="drawerPaper">
              <Paper className={classes.headerPaper}>
                <Typography gutterBottom variant="h4" component="h4">
                  Stake GSVE
                  <Box textAlign='right'>
                    <Button color="secondary" variant="contained" href="https://docs.gassave.org">
                      Find Out More
                      </Button>
                  </Box>
                </Typography>
                <Typography align="left" gutterBottom variant="h6" component="h4">
                  Stake over 250 GSVE to reduce the protocol minting fee
                </Typography>
                <Typography align="left" gutterBottom variant="h6" component="h4">
                  Stake over 1,000 GSVE to claim Gas Tokens from the fee pool!
                </Typography>
                <Typography align="left" gutterBottom variant="h8" component="h4">
                  Stake Rewards of 0.1% daily will be enabled in future!
                </Typography>
                <br />
              </Paper>
            </div>
          </Grid>

          <Grid item xs={12} sm={6} md={6} xl={6}>
            <Card className={classes.headerPaper} variant="outlined">
              <Paper>
                <CardContent>
                  <Typography gutterBottom variant="h5" component="h4">
                    GSVE Stake Stats
                  </Typography>

                  {showUi && (
                    <>
                      <Divider light />
                      <Typography gutterBottom variant="h7" component="h4">
                        Total GSVE Staked : {tokenStaked} Tokens
                    </Typography>
                    </>
                  )}

                  <Divider light />
                  <Typography gutterBottom variant="h7" component="h4">
                    Total GSVE Burned: {tokensBurned} Tokens
                  </Typography>
                  <Divider light />
                  <Typography gutterBottom variant="h7" component="h4">
                    Current GSVE Supply: {tokensLeft} Tokens
                  </Typography>
                  <Divider light />
                  <Typography gutterBottom variant="h7" component="h4">
                    GSVE Rewards Remaining: {rewardsLeft} Tokens
                  </Typography>
                </CardContent>
              </Paper>
            </Card>
          </Grid>
          <Grid item xs={12} sm={6} md={6} xl={6}>
              <Card className={classes.headerPaper} variant="outlined">
                <Paper>
                  <CardContent>
                    {showUi === true && (
                      <>
                        <Typography gutterBottom variant="h5" component="h4"> User Stats </Typography>
                        <Divider light />
                        {userTier}
                        <Divider light />
                        <br />
                        <br />
                      </>
                    )}
                    {showUi === false && (<>
                      <br />
                      <br />
                      <Typography gutterBottom variant="h5" component="h4"> You Aren't Logged in</Typography>

                      <br />
                      <br />
                    </>
                    )}

                  </CardContent>
                </Paper>
              </Card>
            </Grid>
          {showUi === true && (
            <>
              <Grid item xs={12} sm={6} md={6} xl={6}>
                <Paper className={classes.headerPaper}>
                  <Card className={classes.card} variant="outlined">
                    <CardContent>
                      <Typography gutterBottom variant="h6" component="h4">
                        Stake your GSVE
                </Typography>
                      <Divider light />
                      <Typography gutterBottom variant="h7" component="h4">
                        You have A GSVE Balance of : {balance}
                      </Typography>
                      <Divider light />
                      <br />
                      <br />
                      <form onSubmit={e => { handleStakeSubmit(e) }}>
                        <TextField id="stake_amount" variant="outlined" label="GSVE" defaultValue="0" type="number" inputProps={{ step: "any" }} onChange={handleStakeFieldChange} />
                        <Button type="submit" variant="contained" color="primary" className={classes.button}> Stake </Button>
                      </form>
                      <br />
                      <br />
                    </CardContent>
                  </Card>
                </Paper>
              </Grid>
              <Grid item xs={12} sm={6} md={6} xl={6}>
                <Paper className={classes.headerPaper}>
                  <Card className={classes.card} variant="outlined">
                    <CardContent>
                      <Typography gutterBottom variant="h6" component="h4">
                        Unstake your GSVE
                </Typography>
                      <Divider light />
                      <Typography gutterBottom variant="h7" component="h4">
                        You have Staked : {userStake} GSVE Tokens
                </Typography>
                      <Divider light />
                      <br />
                      <br />
                      <Button variant="contained" color="primary" onClick={handleUnStake} className={classes.button}> Unstake All </Button>
                      <br />
                      <br />
                      <br />
                    </CardContent>
                  </Card>
                </Paper>
              </Grid>
            </>
          )}
        </Grid>
      </Paper>
    </div>
  );
}