export async function approveGSVE(numTokens, web3, chainId) {
    
    var contract = await initGSVETokenContract(web3, chainId);
    var account = await getAccount(web3)
    var allowance = await contract.methods.allowance(account[0], contractAddresses[chainId]['core']).call()
    if(web3.utils.fromWei(allowance) < parseFloat(numTokens) ){
        infoSwal('GSVE Approval Transaction Created')
        await contract.methods.approve(contractAddresses[chainId]['core'], web3.utils.toWei("100000000")).send({ from: account[0] }).then(res => txnSwal('Transaction Successful', 'success')).catch(err => txnSwal('Transaction Error', 'error'));
    }
}

export async function stakeGSVE(numTokens, web3, chainId) {
    infoSwal('Stake Transaction Created')
    var contract = await initCoreContract(web3, chainId);
    await approveGSVE(numTokens, web3, chainId);
    var account = await getAccount(web3)
    await contract.methods.stake(web3.utils.toWei(numTokens)).send({ from: account[0] }).then(res => txnSwal('Transaction Successful', 'success')).catch(err => txnSwal('Transaction Error', 'error'));
}

export async function getClaimTime(web3, chainId) {
    var contract = await initCoreContract(web3, chainId);
    var account = await getAccount(web3)
    var claimTime = await contract.methods.getClaimTimes(account[0]).call();

    claimTime = Date.now() - (claimTime * 1000)

    var claimString = ""
    if (claimTime > (60*60*12*1000)){
        claimString = "You can claim up to up to 0.05 GST1, GST2 or 5 Chi, Wrapped GST1, Wrapped GST2, Wrapped Chi"
    }
    else{
        var timer = (12*60*60) - (claimTime / 1000)
        var hours = timer / 60 / 60
        hours = hours.toString().split(".")[0]
        claimString = "You can claim gas tokens in around " + hours + " hours!"
    }

    return claimString
}

export async function getTokenSupplyInfo(gasTokenName, web3, chainId) {
    var contract;
    var supply;
    if (gasTokenName === "Wrapped Chi") {
        contract = await initGasTokenContract(web3, chainId, "wchi")
        supply = await contract.methods.totalSupply().call()
        return supply
    }
    else if (gasTokenName === "Chi") {
        contract = await initGasTokenContract(web3, chainId, "chi")
        supply = await contract.methods.totalSupply().call()
        return supply
    }
    else if (gasTokenName === "Wrapped GST2") {
        contract = await initGasTokenContract(web3, chainId, "wgst2")
        supply = await contract.methods.totalSupply().call()
        return supply
    }
    else if (gasTokenName === "Gas Token 2") {
        contract = await initGasTokenContract(web3, chainId, "gst2")
        supply = await contract.methods.totalSupply().call()
        return supply
    }
    else if (gasTokenName === "Wrapped GST1") {
        contract = await initGasTokenContract(web3, chainId, "wgst1")
        supply = await contract.methods.totalSupply().call()
        return supply
    }
    else if (gasTokenName === "Gas Token 1") {
        contract = await initGasTokenContract(web3, chainId, "gst1")
        supply = await contract.methods.totalSupply().call()
        return supply
    }
    else if (gasTokenName === "GSVE") {
        contract = await initGSVETokenContract(web3, chainId)
        supply = await contract.methods.totalSupply().call()
        return web3.utils.fromWei(supply)
    }
    else {
        return -1
    }
}

