export async function getTokenAddress(gasTokenName, chainId) {
    console.log(gasTokenName)
    console.log(gasTokenName.length)
    if (gasTokenName === "Wrapped Chi") {
        return contractAddresses[chainId]['wchi']
    }
    else if (gasTokenName === "Chi") {
        return contractAddresses[chainId]['chi']
    }
    else if (gasTokenName === "Wrapped GST2") {
        return contractAddresses[chainId]['wgst2']
    }
    else if (gasTokenName === "Gas Token 2") {
        return contractAddresses[chainId]['gst2']
    }
    else if (gasTokenName === "Wrapped GST1") {
        return contractAddresses[chainId]['wgst1']
    }
    else if (gasTokenName === "Gas Token 1") {
        return contractAddresses[chainId]['gst1']
    }
    else if (gasTokenName === "Turn Off") {
        return "0x0000000000000000000000000000000000000000"
    }
    else {
        return ""
    }
}

