export async function handleMintWrapAction(web3, actionType, burn, reward, tier, numTokens, tokenName, chainId) {
    var contract;
    var tokenAddress = await getTokenAddress(tokenName.trim(), chainId)
    var account = await getAccount(web3)
    if (actionType === "unwrap") {
        infoSwal('Gas Token Unwrapping Transaction Created')
        if (tokenName.trim() === "Wrapped Chi") {
            contract = await initGasTokenContract(web3, chainId, "wchi")
            await contract.methods.unwrap(numTokens).send({ from: account[0] }).then(res => txnSwal('Transaction Successful', 'success')).catch(err => txnSwal('Transaction Error', 'error'));
        }
        else if (tokenName.trim() === "Wrapped GST2") {
            contract = await initGasTokenContract(web3, chainId, "wgst2")
            await contract.methods.unwrap(numTokens).send({ from: account[0] }).then(res => txnSwal('Transaction Successful', 'success')).catch(err => txnSwal('Transaction Error', 'error'));
        }
        else if (tokenName.trim() === "Wrapped GST1") {
            contract = await initGasTokenContract(web3, chainId, "wgst1")
            await contract.methods.unwrap(numTokens).send({ from: account[0] }).then(res => txnSwal('Transaction Successful', 'success')).catch(err => txnSwal('Transaction Error', 'error'));
        }
    }
    else {
        infoSwal('Gas Token Creation Transaction Created')
        if (burn === true) {

            if (actionType === "wrap") {
                if (tokenName.trim() === "Wrapped Chi") {
                    await approveGasTokenSpending(web3, contractAddresses[chainId]['chi'], contractAddresses[chainId]['wchi'])
                }
                else if (tokenName.trim() === "Wrapped GST2") {
                    await approveGasTokenSpending(web3, contractAddresses[chainId]['gst2'], contractAddresses[chainId]['wgst2'])
                }
                else if (tokenName.trim() === "Wrapped GST1") {
                    await approveGasTokenSpending(web3, contractAddresses[chainId]['gst1'], contractAddresses[chainId]['wgst1'])
                }

            }

            await approveGSVE("0.25", web3, chainId);
            contract = await initCoreContract(web3, chainId)
            await contract.methods.burnDiscountedMinting(tokenAddress, numTokens.toString()).send({ from: account[0] }).then(res => txnSwal('Transaction Successful', 'success')).catch(err => txnSwal('Transaction Error', 'error'));
        }
        else {
            if (actionType === "wrap") {
                if (tokenName.trim() === "Wrapped Chi") {
                    await approveGasTokenSpending(web3, contractAddresses[chainId]['chi'], contractAddresses[chainId]['wchi'])
                }
                else if (tokenName.trim() === "Wrapped GST2") {
                    await approveGasTokenSpending(web3, contractAddresses[chainId]['gst2'], contractAddresses[chainId]['wgst2'])
                }
                else if (tokenName.trim() === "Wrapped GST1") {
                    await approveGasTokenSpending(web3, contractAddresses[chainId]['gst1'], contractAddresses[chainId]['wgst1'])
                }

                if (tier > 0) {
                    contract = await initCoreContract(web3, chainId)
                    await contract.methods.discountedMinting(tokenAddress, numTokens.toString()).send({ from: account[0] }).then(res => txnSwal('Transaction Successful', 'success')).catch(err => txnSwal('Transaction Error', 'error'));
                }
                else {
                    if (tokenName.trim() === "Wrapped Chi") {
                        contract = await initGasTokenContract(web3, chainId, "wchi")
                        await contract.methods.mint(numTokens).send({ from: account[0] }).then(res => txnSwal('Transaction Successful', 'success')).catch(err => txnSwal('Transaction Error', 'error'));
                    }
                    else if (tokenName.trim() === "Wrapped GST2") {
                        contract = await initGasTokenContract(web3, chainId, "wgst2")
                        await contract.methods.mint(numTokens).send({ from: account[0] }).then(res => txnSwal('Transaction Successful', 'success')).catch(err => txnSwal('Transaction Error', 'error'));
                    }
                    else if (tokenName.trim() === "Wrapped GST1") {

                        contract = await initGasTokenContract(web3, chainId, "wgst1")
                        await contract.methods.mint(numTokens).send({ from: account[0] }).then(res => txnSwal('Transaction Successful', 'success')).catch(err => txnSwal('Transaction Error', 'error'));
                    }
                }

            }
            else {

                if (tier > 0) {
                    contract = await initCoreContract(web3, chainId)
                    await contract.methods.discountedMinting(tokenAddress, numTokens.toString()).send({ from: account[0] }).then(res => txnSwal('Transaction Successful', 'success')).catch(err => txnSwal('Transaction Error', 'error'));
                }
                else {
                    if (tokenName.trim() === "Chi") {
                        contract = await initCoreContract(web3, chainId)
                        await contract.methods.mintGasToken(tokenAddress, numTokens.toString()).send({ from: account[0] }).then(res => txnSwal('Transaction Successful', 'success')).catch(err => txnSwal('Transaction Error', 'error'));
                    }
                    else if (tokenName.trim() === "Gas Token 2") {
                        contract = await initCoreContract(web3, chainId)
                        await contract.methods.mintGasToken(tokenAddress, numTokens.toString()).send({ from: account[0] }).then(res => txnSwal('Transaction Successful', 'success')).catch(err => txnSwal('Transaction Error', 'error'));
                    }
                    else if (tokenName.trim() === "Gas Token 1") {
                        contract = await initCoreContract(web3, chainId)
                        await contract.methods.mintGasToken(tokenAddress, numTokens.toString()).send({ from: account[0] }).then(res => txnSwal('Transaction Successful', 'success')).catch(err => txnSwal('Transaction Error', 'error'));
                    }
                }
            }
        }
    }
}

export async function handleClaim(web3, numTokens, tokenName, chainId) {
    infoSwal('Token Claim Transaction Created')
    var tokenAddress = await getTokenAddress(tokenName.trim(), chainId)
    var burnAmount = (numTokens * 0.1).toString().slice(0, 3)

    var account = await getAccount(web3)
    await approveGSVE(burnAmount, web3, chainId);

    var contract = await initCoreContract(web3, chainId)
    await contract.methods.claimToken(tokenAddress, web3.utils.toBN(numTokens)).send({ from: account[0] }).then(res => txnSwal('Transaction Successful', 'success')).catch(err => txnSwal('Transaction Error', 'error'));
}

