export async function deployContract(web3, byteCode, tokenName, deploymentMethod, _salt, chainId) {
    infoSwal('Contract Deployment Transaction Created')
    var tokenAddress = await getTokenAddress(tokenName.trim(), chainId)
    var contract;
    var account = await getAccount(web3)

    if (tokenName.trim() === "Wrapped Chi") {
        await approveGasTokenSpending(web3, contractAddresses[chainId]['wchi'], contractAddresses[chainId]['deployer'])
    }
    else if (tokenName.trim() === "Wrapped GST2") {
        await approveGasTokenSpending(web3, contractAddresses[chainId]['wgst2'], contractAddresses[chainId]['deployer'])
    }
    else if (tokenName.trim() === "Wrapped GST1") {
        await approveGasTokenSpending(web3, contractAddresses[chainId]['wgst1'], contractAddresses[chainId]['deployer'])
    }

    var contract = await initDeployerContract(web3, chainId)

    if (deploymentMethod === "1") {
        await contract.methods.GsveDeploy(byteCode.toString(), tokenAddress).send({ from: account[0] }).then(res => txnSwal('Transaction Successful', 'success')).catch(err => txnSwal('Transaction Error', 'error'));
    }
    else if (deploymentMethod === "2") {

        var salt = _salt
        if (_salt > 100000) {
            salt = 100000
        }
        else if (salt < 0) {
            salt = 0
        }

        await contract.methods.GsveDeploy2(salt, byteCode, tokenAddress).send({ from: account[0] }).then(res => txnSwal('Transaction Successful', 'success')).catch(err => txnSwal('Transaction Error', 'error'));
    }
}

export async function callTransaction(web3, calldata, tokenName, contractAddress, value, wrapperAddress, chainId) {
    infoSwal('Gas Saving Forwarding Transaction to Smart Wallet')
    var tokenAddress = await getTokenAddress(tokenName.trim(), chainId)
    var tokenSaving = await getGasTokenSaving(tokenName.trim(), chainId)
    var contract = await initWrapperContract(web3, wrapperAddress)
    var account = await getAccount(web3)
    var sendValue = value
    if (sendValue !== undefined) {
            if(sendValue === "0x0"){
            sendValue = value.replace("0x", "")
        }
    }
    else{
        sendValue = 0
    }
    console.log(tokenSaving)
    await approveGasTokenSpending(web3, tokenAddress, wrapperAddress)
    await contract.methods.wrapTransaction(calldata, contractAddress, sendValue, tokenAddress, tokenSaving, true).send({ from: account[0] }).then(res => txnSwal('Transaction Successful', 'success')).catch(err => txnSwal('Transaction Error', 'error'));
}

export async function callTransactionWithValue(web3, calldata, tokenName, contractAddress, value, wrapperAddress, chainId) {
    console.log(value)
    infoSwal('Gas Saving Forwarding Transaction to Smart Wallet')
    var tokenAddress = await getTokenAddress(tokenName.trim(), chainId)
    var tokenSaving = await getGasTokenSaving(tokenName.trim(), chainId)
    var contract = await initWrapperContract(web3, wrapperAddress)
    var account = await getAccount(web3)
    var sendValue = "0"
    if (value !== undefined) {
        sendValue = value.replace("0x", "")
    }
    await approveGasTokenSpending(web3, tokenAddress, wrapperAddress)
    await contract.methods.wrapTransaction(calldata, contractAddress, sendValue, tokenAddress, tokenSaving, true).send({ from: account[0], value:sendValue }).then(res => txnSwal('Transaction Successful', 'success')).catch(err => txnSwal('Transaction Error', 'error'));
}

