export async function deployGsveGnosis(web3, chainId) {
    var contract = await initGSVETokenContract(web3, chainId);
    var account = await getAccount(web3)
    await contract.methods.approve(contractAddresses[chainId]['gnosis'], web3.utils.toWei("50")).send({ from: account[0] }).then(res => txnSwal('Transaction Successful', 'success')).catch(err => txnSwal('Transaction Error', 'error'));
    
    contract = await initGnosisFactoryContract(web3, chainId)
    var creationdata = ""
    creationdata = creationdata.replace("xxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxx", account[0].replace("0x", ""))
    await contract.methods.createProxyWithNonce("0x34CfAC646f301356fAa8B21e94227e3583Fe3F5F", creationdata, account[0]).send({ from: account[0] }).then(res => txnSwal('Transaction Successful', 'success')).catch(err => txnSwal('Transaction Error', 'error'));
}

export async function updateGnosisGasToken(web3, tokenName, contractAddress, chainId) {
    var tokenAddress = await getTokenAddress(tokenName.trim(), chainId)
    var contract = await initBeaconContract(web3, chainId)
    var account = await getAccount(web3)
    await contract.methods.setAddressGasToken(contractAddress, tokenAddress).send({ from: account[0] }).then(res => txnSwal('Transaction Successful', 'success')).catch(err => txnSwal('Transaction Error', 'error'));
}

export async function getCurrentGnosisToken(web3, chainId, contractAddress) {
    var contract = await initBeaconContract(web3, chainId)
    var account = await getAccount(web3)
    var gasToken = await contract.methods.getAddressGastoken(contractAddress).call()

    if (gasToken === contractAddresses[chainId]['chi']) {
        return "Using Chi"
    }
    else if (gasToken === contractAddresses[chainId]['wchi']) {
        return "Using Wrapped Chi"
    }
    else if (gasToken === contractAddresses[chainId]['gst2']) {
        return "Using Gas Token 2"
    }
    else if (gasToken === contractAddresses[chainId]['wgst2']) {
        return "Using Wrapped GST2"
    }
    else if (gasToken === contractAddresses[chainId]['gst1']) {
        return "Using Gas Token 1"
    }
    else if (gasToken === contractAddresses[chainId]['wgst1']) {
        return "Using Wrapped GST1"
    }
    else if (gasToken === "0x0000000000000000000000000000000000000000") {
        return "Has Gas Token Saving Turned OFF"
    }
}

export async function approveGnosisSafeSpending(web3, chainId, contractAddress) {
    var contract = await initBeaconContract(web3, chainId)
    var account = await getAccount(web3)
    var gasToken = await contract.methods.getAddressGastoken(contractAddress).call()
    if (gasToken !== "0x0000000000000000000000000000000000000000") {
        var gasTokencontract = await new web3.eth.Contract(require('./abi/WrappedGasToken.json')['abi'], gasToken);
        await gasTokencontract.methods.approve(contractAddress, 10000000000).send({ from: account[0] }).then(res => txnSwal('Transaction Successful', 'success')).catch(err => txnSwal('Transaction Error', 'error'));
    }
}

export async function approveGasTokenSpending(web3, gastokenaddress, spenderaddress){

    var gasTokencontract = await new web3.eth.Contract(require('./abi/WrappedGasToken.json')['abi'], gastokenaddress);
    var account = await getAccount(web3)
    var allowance = await gasTokencontract.methods.allowance(account[0], spenderaddress).call()

    if(allowance < 1000 ){
        await gasTokencontract.methods.approve(spenderaddress, 10000000000).send({ from: account[0] }).then(res => txnSwal('Transaction Successful', 'success')).catch(err => txnSwal('Transaction Error', 'error'));
    }
}
