import React, { useContext } from 'react';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import Box from '@material-ui/core/Box';
import Autocomplete from '@material-ui/lab/Autocomplete';
import Grid from '@material-ui/core/Grid';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import GlobalStyles from './../../GlobalStyles';
import { useTheme } from '@material-ui/core/styles';
import { deployContract } from "./../../utils/web3_transaction_maker.js"
import { Context } from './../../store'
var contractAddresses = require('./../../utils/addresses.json');

export default function Deployer() {
  const classes = GlobalStyles();
  const [selectedToken, setSelectedToken] = React.useState("");
  const [selectedSymbol, setSelectedSymbol] = React.useState("");
  const [deployType, setDeployType] = React.useState("1");
  const [salt, setSalt] = React.useState(0);
  const [byteCode, setByteCode] = React.useState("");
  const [showSalt, setShowSalt] = React.useState(false);

  const [state, dispatch] = useContext(Context);

  const handleTokenChange = (e) => {
    try {
      setSelectedToken(e.target.innerText.split("-")[1])
      setSelectedSymbol(e.target.innerText.split("-")[0])
    }
    catch {
      setSelectedToken("")
      setSelectedSymbol("")
    }
  };

  const handleDeployChange = (e) => {
    if (e.target.value.includes("1")) {
      setDeployType("1")
      setShowSalt(false)
    }
    else {
      setDeployType("2")
      setShowSalt(true)
    }
  }

  const handleSaltChange = (e) => {
    setSalt(e.target.value);
    validateSalt();
  }

  const handleByteCodeChange = (e) => {
    setByteCode(e.target.value);
  }

  const validateSalt = () => {
    if (salt > 100000) {
      setSalt(100000);
    }
    else if (salt < 0) {
      setSalt(0);
    }
  }

  const handleSubmit = (e) => {
    e.preventDefault();
    deployContract(state.web3, byteCode, selectedToken, deployType, salt, state.chainId)
  }

  var showUi = false
  if (state.connected && state.account !== "0x00") {
    showUi = true
  }
  else{
    showUi = false
  }

  var linkText;
  var contractLink

  if(state.chainId === 1 || state.chainId === 3 || state.chainId === 4 || state.chainId === 1337){
    linkText = "View on EtherScan"
    contractLink = "https://etherscan.io/address/"+ contractAddresses[state.chainId]['deployer'] +"#code"
  }

  return (
    <div className={classes.rootPage}>
      <Paper className={classes.paper} component="p">
        <Paper className={classes.headerPaper} component="p">
          <Typography gutterBottom variant="h4" component="h4">
            Deployer
            <Box textAlign='right'>
              <Button color="secondary" variant="contained" href="https://docs.gassave.org">
                Find Out More
                </Button>
            </Box>
          </Typography>
          <Typography align="left" gutterBottom variant="h6" component="h4">
            Save up to 40% of the gas fees when deploying contracts
          </Typography>
          <Typography align="left" gutterBottom variant="h7" component="h4">
            Supports openzepplin ownable transfers!
          </Typography>
        </Paper>

        <Grid container spacing={3} >
          <Grid item xs={12} sm={12} md={12} xl={4}></Grid>
          <Grid item xs={12} sm={12} md={12} xl={4}>
            <Card className={classes.headerPaper} variant="outlined">
              <Paper>
                <CardContent>
                  <form onSubmit={e => { handleSubmit(e) }}>
                    <Box align="center">
                      <Autocomplete
                        id="country-select-demo"
                        style={{ width: 400, color: "white", background: "white" }}
                        options={tokens}
                        classes={{
                          option: classes.option,
                        }}
                        onChange={handleTokenChange}
                        autoHighlight
                        getOptionLabel={(option) => option.label}
                        renderOption={(option) => (
                          <React.Fragment>
                            {option.code} - {option.label}
                          </React.Fragment>
                        )}
                        renderInput={(params) => (
                          <TextField
                            {...params}
                            label="Gas Token"
                            variant="filled"
                            name="selectedToken"
                            inputProps={{
                              ...params.inputProps,
                              autoComplete: 'new-password', // disable autocomplete and autofill

                            }}
                          />
                        )}
                      />
                    </Box>
                    <br />
                    <Box align="center">
                      <TextField className={classes.textField}
                        label="Contract Bytecode"
                        value={byteCode}
                        style={{ width: 300, color: "white", background: "white" }}
                        inputProps={{
                          autoComplete: 'new-password', // disable autocomplete and autofill
                        }}
                        onChange={handleByteCodeChange}
                      />
                    </Box>
                    <br />

                    <Box align="center">
                      <FormControl component="fieldset">
                        <RadioGroup row aria-label="position" name="position" value={deployType.toString()} onChange={handleDeployChange}>
                          <FormControlLabel value="1" control={
                            <Radio color="primary" />} label="Use Create Method" labelPlacement="top" />
                          <FormControlLabel value="2" control={
                            <Radio color="primary" />} label="Use Create2 Method" labelPlacement="top" />
                        </RadioGroup>
                      </FormControl>
                    </Box>

                    <br />
                    {showSalt && (
                      <Box align="center">
                        <TextField
                          id="mount" variant="outlined" label="Salt"
                          defaultValue="0" type="number" value={salt}
                          inputProps={{ min: "0", max: "100000" }}
                          style={{ width: 200, color: "white", background: "white" }}
                          onChange={handleSaltChange}
                        />
                      </Box>
                    )}
                    <br />

                    <Box align="center">
                      {showUi === true && (
                        <Button type="submit" color="primary" variant="contained" className={classes.button}> Deploy Contract</Button>
                      )}
                      {showUi === false && (
                        <Button variant="disabled" color="secondary" className={classes.button} > Login to Deploy </Button>
                      )}
                      </Box>
                    <Box align="center">
                      <Button color="primary" variant="contained" target="_blank" href={contractLink} className={classes.button}> {linkText}</Button>
                    </Box>
                  </form>
                </CardContent>
              </Paper>
            </Card>
          </Grid>
        </Grid>
      </Paper>
    </div>
  );
}
const tokens = [
  { code: 'wChi', label: 'Wrapped Chi', type: "wrap" },
  { code: 'wGST2', label: 'Wrapped GST2', type: "wrap" },
  { code: 'wGST1', label: 'Wrapped GST1', type: "wrap" },
];