import React from 'react';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import { Paper } from '@material-ui/core';
import Divider from '@material-ui/core/Divider';
import GlobalStyles from './../../GlobalStyles';
import { useTheme } from '@material-ui/core/styles';

export default function Home() {
  const classes = GlobalStyles();
  return (
    <div className={classes.rootPage}>
      <Paper className={classes.headerPaper}>
      <Paper
        id="hero"
        className={classes.paperContainer}
      >
        <br />
        <br />
        <br />
        <br />
        <br />
        <br />
        <br />
        <br />
        <br />
        <br />
        <br />
        <br />
        <br />
        <br />
        <br />
        <Typography gutterBottom variant="h1" component="h1">
          Gas Save
      </Typography>
        <Typography gutterBottom variant="h3" component="h3">
          An Ethereum fee reduction protocol
      </Typography>
        <Button variant="contained" color="primary" className={classes.button} href="#/Litepaper/">View the Litepaper</Button>
        <Button variant="contained" color="primary" className={classes.button} href="https://docs.gassave.org/whitepaper/">Read the Whitepaper</Button>
        <br />
        <br />
        <br />
        <br />
        <br />
        <br />
        <br />
        <br />
        <br />
        <br />
        <br />
        <br />
        <br />
        <br />
        <br />
      </Paper>
      </Paper>
    </div>
  );
}
