import React, { useContext } from 'react';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import clsx from 'clsx';
import Collapse from '@material-ui/core/Collapse';
import IconButton from '@material-ui/core/IconButton';
import Divider from '@material-ui/core/Divider';
import Paper from '@material-ui/core/Paper';

import GlobalStyles from './../../../GlobalStyles';
import { useTheme } from '@material-ui/core/styles';

import { Context } from './../../../store'
import { getTokenSupplyInfo, getTokenBalance } from "./../../../utils/web3_transaction_maker.js"

export default function ImgMediaCard(props) {
  const classes = GlobalStyles();
  const [state, dispatch] = useContext(Context);

  var tokenName = props.tokenName;
  var tokenDescription = props.tokenDescription;
  var tokenImage = props.tokenImage;
  var mintable = props.mintable;
  var claimable = props.claimable;
  var protocolMintFee = props.protocolMintFee;
  var protocolMintReward = props.protocolMintReward;
  var tokenSymbol = props.tokenSymbol;
  var claimBurn = props.claimBurn;
  var gasStored = props.gasStored;
  var contractLink = ""
  var swapLink = ""
  var linkText = ""
  var tradeLinkText = ""
  var priceLink = ""
  var price = -1

  if(state.chainId === 1 || state.chainId === 3 || state.chainId === 4 || state.chainId === 1337){
    linkText = "View on EtherScan"
    swapLink = "https://app.uniswap.org/#/swap?outputCurrency=" + props.tokenAddress +"&use=V2"
    tradeLinkText = "Trade on UniSwap"
    contractLink = "https://etherscan.io/token/" + props.tokenAddress
    priceLink = "https://api.coingecko.com/api/v3/simple/token_price/ethereum?contract_addresses=" + props.tokenAddress + "&vs_currencies=usd"
  }

  const getCoinGeckoPrice = () => {
        var xmlHttp = new XMLHttpRequest();
        xmlHttp.open( "GET", priceLink, false ); // false for synchronous request
        xmlHttp.send( null );
        try{
          price = "$" + JSON.parse(xmlHttp.responseText)[props.tokenAddress]['usd']
        }
        catch{
          price="-"
        }
  }


  if (price === -1){
    price = 0
    getCoinGeckoPrice()
  }

  const [totalSupply, settotalSupply] = React.useState(0);
  if(state.web3_caller !== null){
    getTokenSupplyInfo(props.tokenName, state.web3_caller, state.chainId).then((value) => { settotalSupply(value) });
  }
  const [balance, setBalance] = React.useState(0);
  var showUi = false
  if (state.connected && state.account !== "0x00") {
    showUi = true
    getTokenBalance(props.tokenName, state.web3_caller, state.account, state.chainId).then((value) => { setBalance(value) });
  }
  else{
    showUi = false
  }

  
  var mintableButton
  var mintType
  
    if (mintable) {
      mintableButton = [<Button className={classes.button} variant="contained" color="primary" href="#mint"> Mint </Button>]
      mintType = "Mint"
    }
    else {
      mintableButton = [<Button className={classes.button} variant="contained" color="primary" href="#mint"> Wrap </Button>,
      <Button className={classes.button} variant="contained" color="primary" href="#mint"> Unwrap </Button>]
      mintType = "Wrap"
    }
  
  if (showUi === false){
    mintableButton = [<Button variant="disabled" color="primary" href="#mint"> Login to {mintType} </Button>]
  }

  var tokenStats = []
  /*if(showUi === true){
    tokenStats.push(<Divider light /> <Typography gutterBottom variant="h8" component="h5" align="left"> Your Balance: {balance} </Typography>)
  }*/
  tokenStats.push(<Typography gutterBottom variant="h8" component="h5" align="left"> Base Protocol Fee: {protocolMintFee} {tokenSymbol}</Typography>,
  <Divider light />,
  <Typography gutterBottom variant="h8" component="h5" align="left"> Releasable Gas: {gasStored} gas</Typography>,
  <Divider light />,
  <Typography gutterBottom variant="h8" component="h5" align="left"> Current Token Value: {price} </Typography>,
  <Divider light />,
  <Typography gutterBottom variant="h8" component="h5" align="left"> {mintType} Reward: {protocolMintReward} </Typography>,
  <Divider light />)

  if (claimable) {
    if(showUi === true){
      mintableButton.push(<Button variant="contained" color="primary" href="#claim"> Claim </Button>)
    }
    else if (showUi === false){
      mintableButton.push(<Button variant="disabled" color="primary" href="#claim"> Login to Claim </Button>)
    }
    tokenStats.push(<Typography gutterBottom variant="h8" component="h5" align="left"> GSVE Burn per claimed token: {claimBurn} </Typography>)
    tokenStats.push(<Divider light />)
  }
  else {
    tokenStats.push(<br />)
  }

  const [expanded, setExpanded] = React.useState(false);

  const handleExpandClick = () => {
    setExpanded(!expanded);
  };

  return (
    <Paper className={classes.headerPaper}>
      <Card className={classes.root} variant="outlined">
        <CardContent>
          <Grid container spacing={4}>
            <Grid item xs={12}>
              <Typography gutterBottom variant="h5" component="h2">
                {tokenName}
              </Typography>
            </Grid>

            <Grid item xs={4}>
              <CardMedia
                component="img"
                height="120"
                objectFit='contain'
                image={"images/tokens/" + tokenImage + ".png"}
              />
            </Grid>

            <Grid item xs={8}>
              <Typography variant="body2" color="textSecondary" component="p">
                {tokenStats}
              </Typography>
            </Grid>

            <Grid item xs={12}>
              <IconButton
                className={clsx(classes.expand, {
                  [classes.expandOpen]: expanded,
                })}
                onClick={handleExpandClick}
                aria-expanded={expanded}
                aria-label="show more"
              >
                <Typography variant="body2" color="textSecondary">
                  Token Description
            </Typography>
                <ExpandMoreIcon />
              </IconButton>
              <Collapse in={expanded} timeout="auto" unmountOnExit>
                <CardContent>
                  <Typography variant="body2" color="textSecondary" component="p">
                    {tokenDescription}
                  </Typography>
                </CardContent>
              </Collapse>
            </Grid>

            <Grid item xs={12}>
              {mintableButton}
            </Grid>

            <Grid item xs={6}>
              <Button color="secondary" target="_blank" href={contractLink}>
                {linkText}
            </Button>
            </Grid>
            <Grid item xs={6}>
              <Button color="secondary" target="_blank" href={swapLink}>
                {tradeLinkText}
            </Button>
            </Grid>
          </Grid>
        </CardContent>
      </Card>
    </Paper>
  );
}


